.AS{
    display:flex;
    color:white;
    z-index: 100;
    background: radial-gradient(#164b9c, #16064bf9);
    max-width: 69.75%;
  margin-left: 15%;
  margin-bottom: 1%;
  justify-content: center;
  padding-bottom: 1%;
  align-items: center;
  flex-direction: column;
  position: relative;
  border: 2px solid rgb(0, 17, 126);
  border-radius: 10px;
}
.AS p{
    color: red;
}
.AS button{
    background-color: black;
    color:white;
    padding:10px 10px;
    cursor: pointer;
    border: none;
    font-weight: 200;
    border-radius: 5px;
    text-decoration: none;
}